<template>
  <div class="form__search">
    <InputFormSearch v-model="item" :placeholder="'Поиск'" :img="'lop'"
    :id="'input__search'"></InputFormSearch>
    <InputFormSearch :placeholder="'11.11.2021 - 11.11.2021'"
    :img="'date'" :id="'input__data'"></InputFormSearch>
    <button class="form__btn">
      <span>Фильтры</span>
      <img src="@/assets/formsearch/plus.svg" alt="">
    </button>
    <FilterElement :name="filter.name"
      v-for="filter of this.filters" :key="filter.id"></FilterElement>
  </div>
</template>

<script>
import InputFormSearch from './InputFormSearch.vue';
import FilterElement from './FilterElement.vue';

export default {
  data() {
    return {
      filters: [{ name: '1.5 тонн', id: 0 }],
    };
  },
  props: ['dataValue'],
  components: {
    InputFormSearch,
    FilterElement,
  },
  computed: {
    item: {
      get() {
        return this.dataValue;
      },
      set(value) {
        this.$emit('input', value);
      },
    },
  },
};
</script>

