<template>
  <td nowrap>
    <div class="payment">
      <span :class="getPaymentColor">{{payment}}</span>
      <button class="payment__btn" v-if="payment == 'Не оплачен'">
        <img src="@/assets/load.svg" alt="">
      </button>
    </div>
  </td>
</template>

<script>
export default {
  computed: {
    getPaymentColor() {
      switch (this.payment) {
        case 'Счет выставлен': {
          return 'payment_blue';
        }
        case 'Оплачен': {
          return 'payment_green';
        }
        case 'Не оплачен': {
          return 'payment_red';
        }
        default: {
          return 'payment_orange';
        }
      }
    },
  },
  props: ['payment'],
};
</script>

<style>
.payment {
  display: flex;
  align-items: center;
}
.payment__btn {
  margin-left: 18px;
  width: 16px;
  height: 16px;
}

.payment__btn img {
  width: 16px;
  height: 16px;
}
.payment_blue {
  color: #326BFF;
}

.payment_red {
  color: #F44336;
}

.payment_green {
  color: #2DCA89;
}
</style>
