<template>
  <td nowrap>
    <span  :class="[getStatusColor, {status_orange: this.id == ''}]"
    class="status">{{status}}
    </span></td>
</template>

<script>
export default {
  computed: {
    getStatusColor() {
      switch (this.status) {
        case 'Новый': {
          return 'status_yellow';
        }
        case 'В работе': {
          return 'status_blue';
        }
        case 'Доставлен': {
          return 'status_green';
        }
        case 'Отказ': {
          return 'status_red';
        }
        case 'Отменён': {
          return 'status_red';
        }
        default: {
          return 'status_orange';
        }
      }
    },
  },
  props: ['status', 'id'],
};
</script>

<style>
.status {
  color: white;
  display: block;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  letter-spacing: 0em;
  text-align: center;
  padding: 3px 15px;
  border-radius: 16px;
  min-width: 96px;
}
.status_red {
  background-color: #F94C40;
}
.status_blue {
  background-color: #326BFF;

}
.status_green {
  background-color: #2DCA89;
}
.status_yellow {
  background-color: #FFBF00;
}
.status_orange {
  background-color: #FF9800;
}
</style>
