<template>
  <label class="search__label" :for="id">
    <input v-model="item" :placeholder="placeholder" class="search__input" type="text" :id="id">
    <button class="search__btn"><img :src="getImgUrl(img)" alt=""></button>
  </label>
</template>

<script>
export default {
  computed: {
    item: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('input', value);
      },
    },
  },
  props: ['id', 'placeholder', 'img', 'value'],
  methods: {
    getImgUrl(path) {
      const images = require.context('@/assets/formsearch/');
      return images(`./${path}.svg`);
    },
  },
};
</script>

<style>
.search__label {
  position: relative;
}
.search__input {
  width: 100%;
  border: 1px solid #58595B;
  border-radius: 8px;
  height: 32px;
  width: 216px;
  padding: 2px 30px 2px 10px;
}

.search__input::placeholder {
  color: #E6E6E6;
}

.search__btn {
  position: absolute;
  right: 10%;
  top: 20%;
}
</style>
