<template>
  <td nowrap>
    <div class="executor">
      <span>{{addExecutor}}</span>
      <button v-if="item == ''" class="update">
        <img src="@/assets/executor/update.svg" alt="">
      </button>
      <button v-else class="executor__document">
        <img src="@/assets/executor/document.svg" alt="">
      </button>
    </div>
  </td>
</template>

<script>
export default {
  props: ['item'],
  computed: {
    addExecutor() {
      if (this.item !== '') {
        return this.item;
      }
      return 'Идет подбор исполнителя';
    },
  },
};
</script>

<style>
.executor {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.executor__document {
  margin-left: 26px;
}
</style>
