<template>
  <td nowrap class="gps">
    <button class="gps__btn"><img :src="getImgUrl(getGps)" alt=""></button>
    </td>
</template>

<script>
export default {
  props: ['item'],
  computed: {
    getGps() {
      switch (this.item) {
        case 'gps grey': {
          return 'track';
        }
        case 'gps': {
          return 'Variant3';
        }
        case 'route': {
          return 'Vector';
        }
        case 'route grey': {
          return 'Vector-1';
        }
        default: {
          return 'track';
        }
      }
    },
  },
  methods: {
    getImgUrl(path) {
      const images = require.context('@/assets/gps/');
      return images(`./${path}.svg`);
    },
  },
};
</script>

<style>
.gps {
  text-align: center;
}
</style>
